header {
  position: sticky;
  top: 0;
}

.header {
  padding: var(--offset);
  width: -webkit-fill-available;
  background-color: var(--red);
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 77px;

  /* box-shadow: 0 100px 80px rgb(0 0 0 / 7%),
    0 41.7776px 33.4221px rgb(0 0 0 / 5%), 0 22.3363px 17.869px rgb(0 0 0 / 4%),
    0 12.5216px 10.0172px rgb(0 0 0 / 4%), 0 6.6501px 5.32008px rgb(0 0 0 / 3%),
    0 2.76726px 2.21381px rgb(0 0 0 / 2%); */
}

.indicators {
  display: grid;
  grid-auto-flow: column;
  column-gap: 16px;
}

.calories {
  margin-right: 16px;
}

.indicator {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  color: white;
}

.value {
  font-family: Circe, sans-serif;
  font-size: 22px;
}

.label {
  font-family: Circe, sans-serif;
  font-size: 10px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

.basket {
  display: flex;
  align-self: center;
}
