* {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}

body {
  background: white;
  user-select: none;
}

body::-webkit-scrollbar {
  display: none;
}

.App {
  width: 100%;
  font-family: Circe, sans-serif;
}

:root {
  --red: #d74022;
  --offset: 15px;
  --header-height: 50px;
  --radius: 10px;
}

