.card {
  display: flex;
  flex-direction: column;
  border-radius: var(--radius);
  overflow: hidden;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
}

.card .picture {
  width: 60%;
  object-fit: cover;
  border-bottom-right-radius: var(--radius);
}

.card .line {
  display: flex;
}

.card .indicators {
  width: 40%;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.card .indicators-line {
  display: flex;
  justify-content: space-between;
}

.card .indicators-line span {
  font-size: 18px;
}

.card .info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card .info .buttons {
  max-width: 40%;
  display: flex;
  border: 1px solid rgba(56, 56, 56, 0.2);
  border-radius: 5px;
  padding: 4px;
  align-items: center;
  margin: 0 10px 10px;
  width: fit-content;
  align-self: flex-end;
}

.card .info .buttons button {
  width: 30px;
  height: 30px;
  font-size: 24px;
}

.card .info .buttons span {
  width: 60px;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  height: 100%;
  display: block;
  padding: 0;
  margin: 0;
}

.card .info .title {
  max-width: 60%;
  padding: 10px;
  font-size: 22px;
  line-height: 26px;
}
